import {
  Box,
  Card,
  CardHeader,
  Checkbox,
  Divider,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Typography
} from '@mui/material'
import { ChangeEvent } from 'react'

const SelectingTable = ({ items, title, selectedItems, setSelectedItems, BulkActions, properties, handleClick }) => {
  const selectedSomeApplicants = selectedItems.length > 0 && selectedItems.length < items.length
  const selectedAllApplicants = selectedItems.length === items.length
  const selectedBulkActions = selectedItems.length > 0

  const handleSelectAllApplicants = (event: ChangeEvent<HTMLInputElement>): void => {
    setSelectedItems(event.target.checked ? items && items.map(items => items.id) : [])
  }

  const handleSelectOneApplicant = (event: ChangeEvent<HTMLInputElement>, itemId: number): void => {
    if (!selectedItems.includes(itemId)) {
      setSelectedItems(prevSelected => [...prevSelected, itemId])
    } else {
      setSelectedItems(prevSelected => prevSelected.filter(id => id !== itemId))
    }
  }

  return (
    <Card>
      {selectedBulkActions ? (
        <Box height="55px">
          <Box flex={1} p={1}>
            {BulkActions}
          </Box>{' '}
        </Box>
      ) : (
        <CardHeader title={title} />
      )}

      <Divider />
      <TableContainer sx={{ height: '50vh' }}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell padding="checkbox">
                <Checkbox
                  color="primary"
                  checked={selectedAllApplicants}
                  indeterminate={selectedSomeApplicants}
                  onChange={handleSelectAllApplicants}
                />
              </TableCell>
              {properties.map(property => {
                return <TableCell>{property.header}</TableCell>
              })}
            </TableRow>
          </TableHead>
          <TableBody>
            {items &&
              items.map((item, i) => {
                const isItemSelected = selectedItems.includes(item.id)
                return (
                  <TableRow hover key={item.id} id={item.id} onClick={() => {handleClick(i)}}>
                    <TableCell padding="checkbox">
                      <Checkbox
                        color="primary"
                        checked={isItemSelected}
                        onClick={e => e.stopPropagation()}
                        onChange={(event: ChangeEvent<HTMLInputElement>) => handleSelectOneApplicant(event, item.id)}
                        value={isItemSelected}
                      />
                    </TableCell>

                    {properties.map((property, index) => {
                      return (
                        <TableCell sx={{ cursor: 'pointer' }}>
                          <Typography variant="body1" fontWeight={index === 0 && 'bold'} gutterBottom noWrap>
                            {item[property.property]}
                          </Typography>
                        </TableCell>
                      )
                    })}
                  </TableRow>
                )
              })}
          </TableBody>
        </Table>
      </TableContainer>
    </Card>
  )
}

export default SelectingTable
