import { Box, Button, Modal, styled, TextareaAutosize, Typography } from '@mui/material'
import { useEffect, useState } from 'react'
import { Applicant } from 'src/models/Applicant'
import { Form } from 'src/models/Form'
import BulkActions from './BulkActions'
import NavigateNextIcon from '@mui/icons-material/NavigateNext'
import NavigateBeforeIcon from '@mui/icons-material/NavigateBefore'
import { useTheme } from '@mui/material/styles'
import useMediaQuery from '@mui/material/useMediaQuery'

interface UserModalProps {
  open: boolean
  onClose: () => void
  questions: Form
  Applicants: Applicant[]
  current: number
}

const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: '80%',
  maxWidth: '600px',
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  p: 4
}

const CustomTextareaAutosize = styled(TextareaAutosize)`
  margin: 10px 0px;
  padding: 8px;
  width: 100%;
  border-radius: 5px;
  border: 1px solid lightgrey;
  font-family: 'Pretendard';
`

const UserModal = ({ open, onClose, questions, Applicants, current }: UserModalProps) => {
  const [currentApplicant, setCurrentApplicant] = useState<number>(current) // Index of Applicants[]
  const numberOfApplicants = Applicants.length

  const theme = useTheme()
  const matches = useMediaQuery(theme.breakpoints.up('sm'))

  useEffect(() => {
    setCurrentApplicant(current)
  }, [open])

  const handleNextApplicant = () => {
    setCurrentApplicant((currentApplicant + 1) % numberOfApplicants)
  }

  const handlePrevApplicant = () => {
    if (currentApplicant === 0) {
      setCurrentApplicant(numberOfApplicants - 1)
    } else {
      setCurrentApplicant(currentApplicant - 1)
    }
  }

  return (
    <Modal open={open} onClose={onClose}>
      <Box sx={style}>
        <Box justifyContent="center">
          <Typography variant="h3" sx={{ textAlign: 'center' }}>
            {Applicants[currentApplicant]?.name} 님 의 지원서
          </Typography>
          <Typography variant="subtitle2" sx={{ mb: 2, textAlign: 'center' }}>
            {Applicants[currentApplicant]?.studentId} / {Applicants[currentApplicant]?.department}
          </Typography>
        </Box>
        <Box display="flex" alignItems="center" justifyContent="space-around">
          {matches && (
            <Button onClick={handlePrevApplicant}>
              <NavigateBeforeIcon />
            </Button>
          )}

          <Box width={matches ? '60%' : '90%'}>
            {questions.question1 && (
              <Box>
                <Typography variant="subtitle1">{questions.question1}</Typography>
                <CustomTextareaAutosize
                  maxRows={4}
                  minRows={3}
                  value={Applicants[currentApplicant]?.answer1}
                  disabled
                />
              </Box>
            )}
            {questions.question2 && (
              <Box>
                <Typography variant="subtitle1">{questions.question2}</Typography>
                <CustomTextareaAutosize
                  maxRows={4}
                  minRows={3}
                  value={Applicants[currentApplicant]?.answer2}
                  disabled
                />
              </Box>
            )}
            {questions.question3 && (
              <Box>
                <Typography variant="subtitle1">{questions.question3}</Typography>
                <CustomTextareaAutosize
                  maxRows={4}
                  minRows={3}
                  value={Applicants[currentApplicant]?.answer3}
                  disabled
                />
              </Box>
            )}
          </Box>
          {matches && (
            <Button onClick={handleNextApplicant}>
              <NavigateNextIcon />
            </Button>
          )}
        </Box>

        {matches ? (
          <Box marginTop={2} display="flex" justifyContent="center">
            <BulkActions selectedApplicants={[Applicants[currentApplicant]?.id]} onCloseModal={onClose} />
            <Button variant="outlined" onClick={onClose} sx={{ ml: 1, mt: !matches && 1 }}>
              닫기
            </Button>
          </Box>
        ) : (
          <>
            <Box marginTop={2} display="flex" justifyContent="center">
              <BulkActions selectedApplicants={[Applicants[currentApplicant]?.id]} onCloseModal={onClose} />
            </Box>
            <Box display="flex" justifyContent="space-around" sx={{ mt: 2 }}>
              <Button onClick={handlePrevApplicant}>
                <NavigateBeforeIcon />
              </Button>
              <Button variant="outlined" onClick={onClose} >
                닫기
              </Button>
              <Button onClick={handleNextApplicant}>
                <NavigateNextIcon />
              </Button>
            </Box>
          </>
        )}
      </Box>
    </Modal>
  )
}

export default UserModal
